___CSS_LOADER_EXPORT___.push([module.id, `.endround-modal-container {
    border: none !important;
    background-color: rgba(255, 255, 255, 0.4);
    -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(5px);
    width: 100vw;
    height: 100vh;
    z-index: 0;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: absolute;
    animation: fade-in 1000ms forwards;
    display: flex;
    flex-direction: column;
    text-align: left;
    justify-content: center;
    align-items: center;
  }
  .endround-modal {
    box-shadow: 0 0 #0000, 0 0 #0000, 0 25px 50px -12px rgba(0, 0, 0, 0.25);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: 2s ease-out;
    width: 75vmin;
    height: 75vmin;
    margin: 3vmin;
    letter-spacing: 1px;
    font-family: 'Spiegel', system-ui, -apple-system, BlinkMacSystemFont,
      'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue',
      sans-serif;
    padding: 5px 15px;
    background: #1e2328;
    color: #cdbe91;
    border-image: linear-gradient(to bottom, #c8aa6d, #7a5c29);
    border-image-slice: 1;
    border-width: 5px;
    border-style: solid;
    animation: scale-in 1000ms forwards, fade-in 1000ms forwards;
  }
  .endround-modal-header {
    display: flex;
    justify-content: center;
    font-size: 5vmin;
  }
  .endround-modal-body{
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    align-items: center;
  }
  .endround-modal-body .description{
    margin: 3.5vmin;
  }
  .okay-emote{
    width: 50%;
    margin: 1vmin;
  }
  .endround-modal-footer{
    display: flex;
    justify-content: center;
    align-items: center;
  }
  #close-endround-dialog {
    display: flex;
    justify-content: center;
    align-items: center;
    background: #1e2328;
    color: #cdbe91;
    font-size: 4vmin;
    font-family: 'Marcellus SC', serif;
    cursor: pointer;
    box-shadow: inset 0 0 2px #000000;
    border-image: linear-gradient(to bottom, #c8aa6d, #7a5c29);
    border-image-slice: 1;
    border-width: 2px;
    padding: 0.5vmin;
    margin: 1vmin;
  }
  .close-dialog-button:hover {
    text-shadow: 0 0 5px #ffffff80;
    box-shadow: 0 0 8px 0 #ffffff50;
    background: linear-gradient(to bottom, #1e2328, #433d2b);
    cursor: pointer;
    transition: 0.1s;
  }
  @keyframes fade-out {
    0% {
      opacity: 1;
      display: block;
    }
    100% {
      opacity: 0;
      display: none;
    }
  }
  @keyframes fade-in {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
  @keyframes scale-in {
    0% {
      transform: translate(-50%, -50%) scale(0.5);
    }
    100% {
      transform: translate(-50%, -50%) scale(1);
    }
  }
  @keyframes scale-out {
    0% {
      transform: translate(-50%, -50%) scale(1);
      display: block;
    }
    100% {
      transform: translate(-50%, -50%) scale(0.5);
      display: none;
    }
  }
