___CSS_LOADER_EXPORT___.push([module.id, `.modal-container {
  border: none !important;
  background-color: rgba(255, 255, 255, 0.4);
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  width: 100vw;
  height: 100vh;
  z-index: 0;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  position: absolute;
  animation: fade-in 1000ms forwards;
}
.modal {
  box-shadow: 0 0 #0000, 0 0 #0000, 0 25px 50px -12px rgba(0, 0, 0, 0.25);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: 2s ease-out;
  width: 75vmin;
  height: 75vmin;
  margin: 3vmin;
  letter-spacing: 1px;
  font-family: 'Spiegel', system-ui, -apple-system, BlinkMacSystemFont,
    'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue',
    sans-serif;
  padding: 5px 15px;
  background: #1e2328;
  color: #cdbe91;
  border-image: linear-gradient(to bottom, #c8aa6d, #7a5c29);
  border-image-slice: 1;
  border-width: 5px;
  border-style: solid;
  animation: scale-in 1000ms forwards, fade-in 1000ms forwards;
}
.modal-header {
  display: flex;
  justify-content: center;
}
.modal-header-title {
  font-size: 4.5vmin;
  font-weight: 900;
  margin-inline: 20%;
}
#close-game-mechanics {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #1e2328;
  color: #cdbe91;
  border: none;
  font-size: 4vmin;
  font-family: 'Marcellus SC', serif;
  cursor: pointer;
}
.game-instructions {
  white-space: break-spaces;
  text-align: left;
  font-size: 2.5vmin;
}
.round-title {
  text-decoration: underline;
  font-style: italic;
  font-weight: 500;
  font-size: 3vmin;
}
.modal-footer {
  font-size: 3.5vmin;
  font-weight: 900;
  margin-top: 10vmin;
}
@keyframes fade-out {
  0% {
    opacity: 1;
    display: block;
  }
  100% {
    opacity: 0;
    display: none;
  }
}
@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes scale-in {
  0% {
    transform: translate(-50%, -50%) scale(0.5);
  }
  100% {
    transform: translate(-50%, -50%) scale(1);
  }
}
@keyframes scale-out {
  0% {
    transform: translate(-50%, -50%) scale(1);
    display: block;
  }
  100% {
    transform: translate(-50%, -50%) scale(0.5);
    display: none;
  }
}
