___CSS_LOADER_EXPORT___.push([module.id, `#loading-page-container {
  animation: loadingPageIsDone 500ms ease-in-out 2.75s 1 reverse forwards;
}
#gif-container {
  width: 35%;
  height: 50%;
}
#loading-page-gif {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  position: relative;
  left: 5%;
}
#loading-text {
  font-size: 2.5vmax;
  font-family: 'Beaufort';
  font-weight: 900;
  color: #ae914b;
  text-shadow: 0 0 3px #000000, 0 0 5px #000000;
  width: 100%;
  position: relative;
  bottom: 7.5vmin;
}
#loading-bar-container {
  height: 5vmin;
  width: 50%;
  background-color: #e0e0de;
  border-radius: 15vmax;
  margin-inline: 10vmin;
  position: relative;
  bottom: 5vmin;
  padding: 5px;
}
#progress {
  display: flex;
  align-items: center;
  justify-content: end;
}
#progress-label {
  color: black;
  font-weight: 900;
  font-family: 'Spiegel';
  font-size: 2vmin;
  padding: 1.5vmin;
}
@keyframes loadingPageIsDone {
  0% {
    opacity: 0;
    transform: scale(1.2);
  }

  100% {
    opacity: 1;
    transform: scale(1);
  }
}
